import { getSession, type Env } from '../_utils/session';
import { getUserById, getSubscriptionForUser } from '../_utils/db';

export const onRequest: any = async ({ request, env }: { request: Request; env: Env }) => {
  if (request.method !== 'GET') {
    return new Response(JSON.stringify({ error: 'Method not allowed' }), { status: 405, headers: { 'Content-Type': 'application/json' } });
  }

  const session = await getSession(env, request);
  if (!session) {
    return new Response(JSON.stringify({ error: 'Unauthorized' }), { status: 401, headers: { 'Content-Type': 'application/json' } });
  }

  const user = await getUserById(env, session.user_id);
  if (!user) {
    return new Response(JSON.stringify({ error: 'Unauthorized' }), { status: 401, headers: { 'Content-Type': 'application/json' } });
  }

  const sub = await getSubscriptionForUser(env, user.id);
  return new Response(JSON.stringify({
    user: {
      id: user.id,
      email: user.email,
      phone: user.phone,
      name: user.name
    },
    subscription: sub ? {
      status: sub.status,
      dodo_subscription_id: sub.dodo_subscription_id,
      latest_payment_id: sub.latest_payment_id,
      updated_at: sub.updated_at
    } : null
  }), { status: 200, headers: { 'Content-Type': 'application/json' } });
};